@import "main";
// End Of Import

// Preloader
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $color-prim;
  @include Flex;
  z-index: 9999;

  .spinner {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 4px solid $color-prim;
    border-left: 4px solid $color-sec;
    animation: spin 1s linear infinite;
  }
}

// Mob Nav
.menuTriger {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgba($color-prim, 0.8);
  position: fixed;
  top: 25px;
  right: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 99;
  display: none;

  html[dir="ltr"] & {
    left: 30px;
    right: unset;
  }

  @include Sm {
    display: flex;
  }

  i {
    color: #ffffff;
    font-size: 20px;
  }
}

.navover {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba($color-sec, 0.95);
  z-index: 9;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;

  &.open {
    opacity: 1;
    visibility: visible;
  }
}

.sideNav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: $color-sec;
  z-index: 99;
  overflow-y: auto;
  transition: 0.5s;
  @include Flex;
  flex-direction: column;
  transform-origin: right;
  transform: scaleX(0);

  html[dir="ltr"] & {
    transform-origin: left;
  }

  &.open {
    // transform-origin: left;
    transform: scaleX(1);

    ul {
      opacity: 1;

      li {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }

  &.origin {
    transform-origin: left;

    html[dir="ltr"] & {
      transform-origin: right;
    }
  }

  .options {
    padding: 0 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: absolute;
    top: 50px;

    .lang a {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      color: #fff;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      background-color: $color-prim;
    }

    .search_pop {
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background-color: $color-prim;
      color: #fff;
      font-size: 14px;
      margin-left: auto;
      margin-right: 20px;

      html[dir="ltr"] & {
        margin-left: 20px;
        margin-right: auto;
      }
    }

    .close1 {
      display: flex;
      font-size: 18px;
      color: $color-prim;
      cursor: pointer;
    }
  }

  & > ul {
    display: flex;
    align-items: center;
    flex-direction: column;
    opacity: 0;
    transition: 0.5s;
    transition-delay: 0.5s;

    & > li {
      position: relative;
      padding: 10px 0;
      // margin-bottom: 20px;
      transition: 0.5s;
      transform: translateX(10px);
      opacity: 0;

      &:nth-child(1) {
        transition-delay: 0.3s;
      }
      &:nth-child(2) {
        transition-delay: 0.5s;
      }
      &:nth-child(3) {
        transition-delay: 0.7s;
      }
      &:nth-child(4) {
        transition-delay: 0.9s;
      }
      &:nth-child(5) {
        transition-delay: 1.1s;
      }
      &:nth-child(6) {
        transition-delay: 1.3s;
      }

      a {
        width: 100%;
        display: block;
        font-size: 20px;
        color: $color-white;
        transition: 0.5s;
      }

      i {
        position: absolute;
        top: 13px;
        left: 13px;
        transform: rotate(90deg);
        font-size: 9px;
        color: currentColor;
        transition: 0.5s;

        html[dir="ltr"] & {
          left: unset;
          right: 13px;
        }

        &.open {
          transform: rotate(0deg);
        }
      }

      .drop {
        width: 100%;
        padding: 5px 15px;
        display: none;
        overflow: visible;
        height: auto;

        li {
          padding: 4px 0;

          a {
            font-size: 15px;
            color: #898989;
            transition: 0.5s;

            &:hover {
              color: #595959;
            }
          }
        }
      }
    }
  }
}

// Start Up

.up {
  background-color: $color-prim;

  @include Sm {
    display: none;
  }

  .details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
  }

  .right {
    span {
      color: $color-white;
      font-size: 13px;
    }
  }

  .lang {
    @include Flex;
    direction: ltr;

    html[dir="ltr"] & {
      direction: rtl;
    }

    a {
      font-size: 12px;
      color: $color-white;
      transition: 0.5s;

      &:hover {
        color: rgba($color-white, 0.7);
      }
    }

    .social {
      margin-left: 20px;
      padding-left: 20px;
      border-left: 1px solid #fb8e61;
      direction: rtl;

      html[dir="ltr"] & {
        margin-left: 0px;
        padding-left: 0px;
        margin-right: 20px;
        padding-right: 20px;
        border-right: 1px solid #fb8e61;
        border-left: 0;
        direction: ltr;
      }

      a {
        color: #fabea5;

        &:not(:last-child) {
          margin-left: 17px;

          html[dir="ltr"] & {
            margin-left: 0;
            margin-right: 17px;
          }
        }

        &:hover {
          color: #fff;
        }
      }
    }
  }
}

// End Of Up

// Navbar

.navbar {
  padding: 15px 0;
  margin-bottom: 0;
  box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);

  @include Md {
    position: relative;
    z-index: 99;
  }

  .content {
    @include Flex;
    justify-content: space-between;

    @include Sm {
      flex-direction: column;
    }
  }

  .logo{

    .logo-lg{
      @include Md{
        display: block;
      }

      @include Sm{
        display: none;
      }
    }

    .logo-mob{
      display: none;
      @include Sm{
        display: block
      }
    }
  }

  .links {
    @include Md {
      margin: auto;
    }

    @include Lg {
      margin-right: auto;
      margin-left: 27px;

      html[dir="ltr"] & {
        margin-right: 27px;
        margin-left: auto;
      }
    }

    @include Sm {
      display: none;
    }

    ul {
      @include Flex;

      li {
        &:not(:last-child) {
          margin-left: 22px;

          @include Md {
            margin-left: 15px;
          }

          @include Lg {
            margin-left: 22px;
          }

          html[dir="ltr"] & {
            margin-left: 0;
            margin-right: 22px;

            @include Md {
              margin-right: 15px;
            }

            @include Lg {
              margin-right: 22px;
            }
          }
        }

        a {
          font-size: 16px;
          color: #3d3d3d;
          transition: 0.5s;

          @include Md {
            font-size: 14px;
          }

          @include Lg {
            font-size: 16px;
          }

          &:hover {
            color: $color-prim;
          }
        }
      }
    }
  }

  form {
    @include Sm {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba($color-sec, 0.98);
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 999;
      opacity: 0;
      visibility: hidden;
      transition: 0.5s;
    }

    &.open {
      opacity: 1;
      visibility: visible;

      .search {
        transform: scaleX(1);
      }
    }

    & > i {
      position: absolute;
      top: 15%;
      font-size: 20px;
      color: #fa5d1e;
      display: none;

      @include Sm {
        display: block;
      }
    }
  }
  .search {
    position: relative;
    transition: 0.5s;

    @include Sm {
      transform: scaleX(0);
      top: -15%;
      width: 70%;
    }

    @include Xs {
      width: 85%;
    }

    input {
      width: 250px;
      border: 0;
      border-radius: 100px;
      background-color: #f6f6f6;
      height: 42px;
      padding: 0 25px;
      transition: 0.5s;
      color: #afafaf;

      @include Md {
        width: 200px;
      }

      @include Lg {
        width: 250px;
      }

      @include Sm {
        width: 100%;
      }

      &:focus {
        background-color: rgba($color-prim, 0.1);

        @include Sm {
          background-color: rgba($color-white, 1);
        }
      }

      &::placeholder {
        color: #afafaf;
      }
    }

    button {
      width: 30px;
      height: 30px;
      background-color: $color-prim;
      border-radius: 50%;
      border: 0;
      @include Flex;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 7px;
      transition: 0.5s;

      html[dir="ltr"] & {
        left: unset;
        right: 7px;
      }

      &:hover {
        background-color: rgba($color-prim, 0.8);
      }

      i {
        font-size: 12px;
        color: #fff;
      }
    }
  }
}

// Navbar

.owl-nav {
  position: absolute;
  top: 50%;
  transform: translate(50%, -50%);
  right: 50%;
  width: 100%;
  height: 0;
  display: flex !important;
  justify-content: space-between;
  align-items: center;

  @include Sm {
    display: none !important;
  }

  button {
    span {
      line-height: 1;
    }
  }
}

.owl-dots {
  position: absolute;
  bottom: 40px;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;

  @include Xs {
    bottom: 15px;
  }

  button {
    &:not(:last-child) {
      margin-left: 5px;
    }

    &.active {
      span {
        background-color: $color-prim;
      }
    }

    span {
      display: block;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: rgba($color-sec, 0.6);
    }
  }
}

// Main Slider

.main-slider {
  margin-top: 0px;
  // position: relative;
  // z-index: -1;

  .owl-nav {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    padding: 0 60px;
    height: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include Sm {
      display: none;
    }

    button {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: rgba(#3c3c3c, 0.5) !important;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: 0.5s;

      &:hover {
        background-color: $color-prim !important;
      }

      i {
        font-size: 10px;
        color: #fff;
      }
    }
  }

  .owl-dots {
    position: absolute;
    bottom: 40px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    @include mob600 {
      bottom: 10px;
    }

    button {
      &:not(:last-child) {
        margin-left: 5px;
      }

      &.active {
        span {
          background-color: $color-prim;
        }
      }

      span {
        display: block;
        width: 8px;
        height: 8px;
        background-color: #757a7b;
        border-radius: 50%;
      }
    }
  }
}

// Main Slider

// Overview

.overview {
  padding: 75px 0;
  text-align: center;

  @include Sm {
    padding: 45px 0;
  }

  h1 {
    margin: 0 0 46px;
    position: relative;
    font-size: 24px;
    font-family: "frtBlk";

    @include Xs {
      line-height: 1.6;
    }

    &:after {
      content: "";
      position: absolute;
      bottom: -25px;
      left: 50%;
      transform: translateX(-50%);
      width: 85px;
      height: 1px;
      background-color: $color-prim;
    }
  }

  p {
    font-size: 17px;
    color: rgb(61, 61, 61);
    font-family: "frtLight";
    line-height: 2;
    margin-bottom: 35px;
  }

  a {
    width: 123px;
    height: 38px;
    border-radius: 100px;
    background-color: #f6f6f6;
    @include Flex;
    margin: auto;
    color: #c3c2c1;
    font-size: 14px;
    font-family: "frtBold";
    position: relative;
    overflow: hidden;
    transition: 0.5s;

    span {
      position: relative;
      z-index: 2;
      transition: 0.5s;
    }

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $color-prim;
      transform: scaleX(0);
      transform-origin: right;
      transition: 0.5s;
      z-index: 1;

      html[dir="ltr"] & {
        transform-origin: left;
      }
    }

    &:hover {
      color: #fff;

      &:after {
        transform: scaleX(1);
      }
    }
  }
}

// Overview

// Products

.products {
  padding: 70px 0;
  background-image: linear-gradient(to bottom, #f7f7f7, #fff);

  &.in {
    background-image: none;
  }

  @include Sm {
    padding: 40px 0;
  }

  &.recent {
    background-color: #f6f6f6;

    @include Sm {
      padding: 40px 0 55px;
    }
  }

  .disc-head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;

    & > p {
      font-size: 20px;
      font-family: "frtBold";
      color: #3c3c3c;
      margin-bottom: 0;

      @include mob375 {
        font-size: 20px;
      }
    }
  }

  a.link {
    width: 140px;
    height: 38px;
    border-radius: 100px;
    background-color: #f6f6f6;
    @include Flex;
    margin: auto;
    color: #c3c2c1;
    font-size: 14px;
    font-family: "frtBold";
    position: relative;
    overflow: hidden;
    transition: .5s;

    span {
      position: relative;
      z-index: 2;
    }

    &:hover {
      background-color: $color-prim;
      color: #FFF;
    }

    @include Sm {
      margin-top: 20px;
    }
  }

  .owl-carousel {
    .owl-item {
      display: flex;
      justify-content: center;
    }

    .owl-nav {
      position: absolute;
      top: 33%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 105%;
      height: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 30px;
      margin-top: 0;
      color: #c4c4c4;
      padding: 0;
      right: unset;

      @include Md {
        width: 102%;
      }

      @include Lg {
        width: 105%;
      }

      @include Sm {
        display: none;
      }

      button {
        width: 15px;
        transition: 0.5s;

        i {
          font-size: 12px;
          color: #bebebe;
          transition: 0.5s;
        }

        &:hover {
          i {
            color: $color-prim;
          }
        }
      }
    }

    .owl-dots {
      display: none;

      @include Sm {
        display: flex;
        justify-content: center;
        align-items: center;
        position: unset;
        transform: translateY(-15px);
      }
    }

    .owl-item {
      img {
        width: initial;
        margin: auto;
      }
    }
  }
}

.product {
  width: 100%;
  position: relative;
  text-align: center;
  background-color: transparent;
  overflow: hidden;
  transition: 0.5s;
  margin-bottom: 45px;

  .pic {
    height: 260px;
    border: 1px solid #e5e5e5;
    overflow: hidden;

    img {
      transition: 0.5s;
      backface-visibility: hidden;
    }
  }

  .detail {
    padding: 20px 0 0;
    background-color: transparent;

    .rate {
      margin-top: 15px;
      direction: rtl;

      html[dir="ltr"] & {
        direction: ltr;
      }

      i {
        color: #f5cd56;
        margin-left: 3px;
        font-size: 11px;

        &:last-child {
          color: #c9c9c9;
          margin-left: 0;
        }

        html[dir="ltr"] & {
          margin-left: 0;
          margin-right: 3px;

          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
  }

  & > a {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  & > img {
    width: initial;
    height: 167px;
    display: block;
    margin: auto;
  }

  .pro-title {
    font-size: 16px;
    color: #3d3d3d;
    transition: 0.5s;
    margin-top: 20px;
  }

  &:hover {
    .pro-title {
      color: $color-prim;
    }
  }

  &:hover {
    .pic {
      img {
        transform: scale(1.02);
        animation: flash 0.5s linear;
      }
    }
  }
}

// Products

// banner

.banner {
  padding: 70px 0;

  @include Sm {
    padding: 40px 0 10px;
  }

  a {
    display: block;
    overflow: hidden;

    img {
      transition: 0.3s cubic-bezier(0.6, -0.28, 0.735, 0.045);
    }

    &:hover {
      img {
        animation: flash 0.5s;
        transform: scale(1.03);
      }
    }
  }
}

