// footer

footer {
  padding: 70px 0 0;
  padding-bottom: 0;
  background-color: #3c3c3c;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 0;
  margin-top: 30px;

  @include Sm {
    padding: 40px 0 0;
    margin-top: 0;
  }

  .foot-links {
    // @include Lg {
    //   text-align: right;
    // }

    @include Sm {
      margin-bottom: 15px;
    }

    .collapse {
      display: block;

      @include Sm {
        display: none;
      }

      &.in {
        display: block;
      }
    }

    & > button {
      font-size: 16px;
      font-family: "frtBold";
      color: #fff;
      margin-bottom: 25px;
      background-color: transparent;
      border: 0;
      padding: 0;
      pointer-events: none;

      &.trans {
        &:after {
          content: "\2212";
        }
      }

      @include Sm {
        width: 100%;
        margin: auto;
        pointer-events: all;
        margin-bottom: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
      }

      @include mob480 {
        width: 100%;
      }

      &:after {
        content: "\002B";
        margin-left: 5px;
        font-size: 18px;
        float: left;
        display: none;

        @include Sm {
          display: block;
        }
      }
    }

    ul {
      @include Sm {
        padding-top: 10px;
        padding-right: 10px;
      }

      li {
        &:not(:last-child) {
          margin-bottom: 20px;
        }
        display: flex;

        i {
          font-size: 14px;
          color: $color-prim;
          margin-left: 10px;

          html[dir="ltr"] & {
            margin-left: 0;
            margin-right: 10px;
          }

          &.fa-phone {
            html[dir="ltr"] & {
              transform: rotateY(180deg);
            }
          }
        }

        a {
          font-size: 14px;
          font-family: "frt";
          backface-visibility: hidden;
          color: #fff;
          position: relative;
          -webkit-background-clip: text;
          background-clip: text;
          -webkit-text-fill-color: transparent;
          background-image: linear-gradient(
            to right,
            $color-white,
            $color-white 50%,
            #fff 50%
          );
          background-size: 200% 100%;
          background-position: 100%;
          display: inline-block;

          & > p {
            margin: 0;
          }

          // html[dir="ltr"] & {
          //     background-size: 200% 100%;
          //     background-position: 0%;
          //     background-image: linear-gradient(to left, $color-prim, $color-prim 50%, #998986 50%);
          // }

          &:hover {
            transition: all 0.5s cubic-bezier(0, 0, 0.23, 1);
            background-position: 0%;

            // html[dir="ltr"] & {
            //     background-position: -100%;
            // }
          }

          &:hover {
            color: $color-prim;
          }
        }

        &.info {
          &:not(:last-child) {
            margin-bottom: 13px;
          }
          a {
            background-image: linear-gradient(
              to right,
              $color-white,
              $color-white 50%,
              #a4a4a4 50%
            );
          }
        }
      }
    }
  }

  .newsletter {
    @include Md {
      margin-top: 35px;
      text-align: center;
    }

    @include Lg {
      margin-top: 0;
      text-align: right;

      html[dir="ltr"] & {
        text-align: left;
      }
    }

    @include Sm {
      margin-top: 35px;
      text-align: center;
    }

    & > p {
      font-size: 16px;
      font-family: "frtBold";
      color: #fff;
      margin-bottom: 20px;
    }

    & > span {
      font-size: 14px;
      font-family: "frt";
      color: #fff;
      margin-bottom: 15px;
      display: block;
    }
  }

  .frm {
    position: relative;
    margin-bottom: 17px;
    width: 360px;

    @include Md {
      margin: auto;
      margin-bottom: 17px;
    }

    @include Sm {
      margin: auto;
      margin-bottom: 17px;
    }

    @include mob375 {
      width: 280px;
    }

    input {
      padding: 0 20px;
      height: 40px;
      width: 100%;
      border: 0;
      color: #afafaf;
      font-size: 14px;
      background-color: #fff;
      border-radius: 200px;
      transition: 0.5s;

      &::placeholder {
        color: #afafaf;
      }
    }

    button {
      position: absolute;
      top: 50%;
      left: 6px;
      transform: translateY(-50%);
      width: 30px;
      height: 30px;
      border-radius: 50%;
      background-color: $color-prim;
      border: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: 0.5s;

      html[dir="ltr"] & {
        left: unset;
        right: 6px;
      }

      &:hover {
        background-color: rgba($color-prim, 0.8);
      }

      i {
        font-size: 12px;
        color: #fff;
      }
    }
  }
}

// End Of Footer

// End

.end {
  border-top: 1px solid #494949;
  margin-top: 80px;

  @include Sm {
    margin-top: 40px;
  }

  @include Xs {
    margin-top: 10px;
  }

  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;

    @include mob600 {
      flex-direction: column-reverse;
      padding: 10px 0;
    }

    @include Xs {
      text-align: center;
    }

    p,
    a {
      font-size: 13px;
      color: #8c8c8c;
      margin-bottom: 0;

      @include Xs {
        font-size: 13px;
      }

      @include mob600 {
        margin-top: 10px;
      }
    }

    p {
      @include mob480 {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin-bottom: 0;
      }
    }

    a {
      img {
        margin-right: 5px;

        // @include mob600 {
        //   margin-right: 0;
        // }

        html[dir="ltr"] & {
          margin-right: 0;
          margin-left: 5px;
        }
      }

      @include mob480 {
        margin-top: 10px;
      }
    }

    .social {
      a {
        i {
          font-size: 16px;
          color: #8c8c8c;
          transition: 0.5s;
        }

        &:not(:last-child) {
          margin-left: 22px;

          html[dir="ltr"] & {
            margin-left: 0;
            margin-right: 22px;
          }
        }

        &:hover {
          i {
            color: #fff;
          }
        }
      }
    }
  }
}

