//////////////////// Start Contact Page

// BreadCrumb
.breadcrmb {
  margin-top: 30px;

  .content {
    border-radius: 100px;
    height: 40px;
    background-color: #f6f6f6;
  }

  ul {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 30px 0 0;

    html[dir="ltr"] & {
      padding: 0 0 0 30px;
    }

    li {
      position: relative;

      &:last-child {
        a {
          // color: $color-sec;
          pointer-events: none;
        }
      }

      &:not(:last-child) {
        margin-left: 30px;

        html[dir="ltr"] & {
          margin-left: 0;
          margin-right: 30px;
        }

        &:after {
          content: "/";
          color: #a9a9a9;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: -17px;

          html[dir="ltr"] & {
            left: unset;
            right: -17px;
          }
        }
      }
      a {
        font-size: 13px;
        color: #a5a5a5;
        transition: 0.5s;

        &:hover {
          color: $color-sec;
        }
      }
    }
  }
}
// BreadCrumb

// Contact

.contact {
  h2 {
    font-size: 20px;
    color: #3d3d3d;
    font-family: "frtBlk";
    margin: 40px 0 30px;
  }

  p {
    font-family: "frtLight";
    font-size: 17px;
    color: #3d3d3d;
    text-transform: uppercase;
  }

  form {
    margin-top: 45px;

    @include Sm {
      margin: 50px 0;
    }

    input,
    label {
      display: block;
      width: 100%;
    }

    label {
      font-family: "frtBold";
      font-size: 15px;
      color: $color-sec;
      margin-bottom: 5px;
      margin-right: 30px;
      font-weight: unset;

      html[dir="ltr"] & {
        margin-right: 0;
        margin-left: 30px;
      }

      &:not(:first-child) {
        margin-top: 20px;
      }

      &:not(:nth-child(5)) {
        &:after {
          content: "*";
          color: red;
        }
      }
    }

    input {
      height: 55px;
      border-radius: 100px;
      background-color: #fbfbfb;
      box-shadow: none;
      border: 1px solid #e5e7e7;
      padding: 0 30px;

      &:focus{
        box-shadow: none;
        outline: none;
        border: 1px solid $color-sec;
      }
    }

    textarea {
      border: 1px solid rgb(230, 232, 232);
      border-radius: 27px;
      height: 142px;
      background-color: #fbfbfb;
      box-shadow: none;
      resize: none;
    }

    .buttn {
      display: flex;
      justify-content: flex-end;

      @include Md {
        margin-bottom: 50px;
      }
    }

    button {
      width: 160px;
      height: 50px;
      background-color: $color-prim;
      border-radius: 100px;
      border: 0;
      font-family: "frtBold";
      font-size: 20px;
      color: #fff;
      margin-top: 25px;
      transition: 0.5s;

      &:hover {
        background-color: rgba($color-prim, 0.8);
      }
    }
  }

  .infos {
    @include Md {
      margin-top: 85px;
      padding-right: 20px;

      html[dir="ltr"] & {
        padding-right: 0;
        padding-left: 20px;
      }
    }

    @include Sm {
      margin: 50px 0;
    }

    .info {
      display: flex;

      &:not(:first-child) {
        margin-top: 20px;
      }

      .icon {
        margin-left: 10px;

        html[dir="ltr"] & {
          margin-left: 0;
          margin-right: 10px;
        }

        i {
          font-size: 18px;
          color: #fb5e1c;

          &.fa-phone {
            html[dir="ltr"] & {
              transform: rotateY(180deg);
            }
          }
        }
      }

      h3 {
        font-size: 16px;
        font-family: "frtBold";
        color: $color-sec;
        margin: 0;
        margin-bottom: 8px;
      }

      p {
        font-size: 16px;
        color: $color-sec;
        margin-bottom: 5px;
        font-family: "frt";
        text-transform: initial;
      }
    }
  }
}

// Contact

// Start Careers
.careers {

  .col-md-6{
    @include Sm{
      padding: 0;
    }
  }

  h2 {
    font-size: 20px;
    font-family: "frtBold";
    color: $color-sec;
    margin: 40px 0 30px;

    & + p {
      font-size: 17px;
      font-family: "frtLight";
      color: $color-sec;
      margin: 0 0 60px;
    }
  }

  form {
    .field {
      margin-bottom: 20px;

      label {
        font-size: 15px;
        color: $color-sec;
        font-family: "frtBold";
        margin: 0 30px 5px 0;
        font-weight: unset;

        html[dir="ltr"] & {
          margin: 0 0 5px 30px;
        }

        &:after {
          content: "*";
          color: #ff4444;
        }

        &.custom {
          margin: 0;
          padding: 10px;
          font-family: "opensans";
          height: 55px;
          background-color: #fcfcfc;
          display: flex;
          justify-content: space-between;
          align-items: center;
          color: #adacad;
          display: flex;
          align-items: center;
          justify-content: flex-end;
          border: 1px solid #e5e7e7;
          border-radius: 100px;

          html[dir="ltr"] & {
            margin-left: 0;
          }

          &:after {
            content: "";
            color: transparent;
          }

          span {
            display: inline-block;
            width: 113px;
            height: 35px;
            line-height: 31px;
            font-size: 15px;
            font-family: "frtLight";
            color: #8a8a8a;
            background-color: #dddddd;
            text-align: center;
            cursor: pointer;
            border-radius: 100px;
            transition: 0.5s;

            &:hover {
              background-color: $color-prim;
              color: #fff;
            }
          }
        }
      }

      input {
        height: 55px;
        border: 1px solid #e5e7e7;
        background-color: #fcfcfc;
        border-radius: 100px;
        box-shadow: none;
        padding: 0 30px;
        transition: 0.5s;

        &:focus {
          outline: none;
          box-shadow: none;
          border: 1px solid $color-sec;
        }

        &#cv {
          display: none;
        }
      }

      &.txt {
        p {
          font-size: 15px;
          color: $color-sec;
          font-family: "frtBold";
          margin: 0 30px 5px 0;

          html[dir="ltr"] & {
            margin: 0 0 5px 30px;
          }
        }

        textarea {
          padding: 10px 30px;
          width: 100%;
          height: 135px;
          resize: none;
          border-radius: 27px;
          border: 1px solid #e6e8e8;
          transition: 0.5s;

          // &:focus{
          //     outline: none;
          //     box-shadow: none;
          //     border: 1px solid #000;
          // }
        }
      }
    }

    button {
      width: 100%;
      height: 50px;
      background-color: $color-prim;
      border: 0;
      font-size: 20px;
      font-family: "frtBold";
      border-radius: 100px;
      color: #fff;
      float: left;
      border: 1px solid transparent;
      transition: 0.5s;
      margin-bottom: 40px;

      @include Md {
        margin-bottom: 90px;
      }

      &:hover {
        background-color: rgba($color-prim, 0.8);
      }

      @include mob480 {
        width: 100%;
      }
    }
  }
}
// End Of Careers

//  CLients Page
.clts {
  h2 {
    font-size: 20px;
    font-family: "frtBold";
    color: $color-sec;
    margin: 40px 0 30px;
    text-align: center;
  }

  .more {
    width: 123px;
    height: 38px;
    border-radius: 100px;
    background-color: #f6f6f6;
    color: #c0bfbe;
    font-size: 14px;
    font-family: "frtBold";
    @include Flex;
    margin: auto;
    margin-top: 25px;
    margin-bottom: 115px;
    transition: 0.5s;

    @include Sm {
      margin: 75px auto;
    }

    &:hover {
      background-color: $color-prim;
      color: #fff;
    }
  }

  .owl-dots {
    bottom: -40px;
  }
}
.clients_in {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.clients_in .client.in {
  .pic {
    display: block;
    width: 100%;
    height: 217px;
    @include Flex;
    border: 1px solid #e5e5e5;
    transition: 0.5s;
    overflow: hidden;

    img {
      width: initial !important;
    }
  }

  p {
    font-size: 16px;
    color: $color-sec;
    margin-top: 15px;
    margin-bottom: 0;
    text-align: center;
    transition: 0.5s;
  }

  &:hover {
    .pic {
      border: 1px solid $color-prim;
    }

    p {
      color: $color-prim;
    }
  }
}

@include Md {
  .clients_in .client.in {
    margin-bottom: 25px;
    width: calc((100% - 45px) / 4);
  }

  .clients_in .client.in:not(:nth-of-type(4n)) {
    margin-left: 15px;
  }

  html[dir="ltr"] .clients_in .client.in:not(:nth-of-type(4n)) {
    margin-right: 15px;
    margin-left: 0px;
  }

  .clients_in .client.in:nth-of-type(4n) {
    margin-left: 0;
  }
}

@include Lg {
  .clients_in .client.in {
    margin-bottom: 25px;
    width: calc((100% - 60px) / 5);
  }

  .clients_in .client.in:not(:nth-of-type(5n)) {
    margin-left: 15px;
  }

  .clients_in .client.in:nth-of-type(5n) {
    margin-left: 0;

    html[dir="ltr"] & {
      margin-right: 0;
    }
  }

  html[dir="ltr"] .clients_in .client.in:not(:nth-of-type(5n)) {
    margin-right: 15px;
    margin-left: 0px;
  }
}

