html {
  overflow-y: hidden;
  scrollbar-width: thin;
  scrollbar-color: #fa5d1e #3d3d3d;
}

html::-webkit-scrollbar {
  width: 8px;
}

html::-webkit-scrollbar-track {
  background-color: #3d3d3d;
}

html::-webkit-scrollbar-thumb {
  background-color: #FA5D1E;
}

body {
  font-family: 'frt', sans-serif;
  direction: rtl;
  overflow-x: hidden;
}

html[dir="ltr"] body {
  direction: ltr;
}

* {
  font-family: inherit;
}

input, a, button {
  font-family: 'frt', sans-serif;
}

input:focus, a:focus, button:focus {
  outline: none;
  text-decoration: none;
}

input:hover, a:hover, button:hover {
  text-decoration: none;
}

button {
  cursor: pointer;
}

button:focus, button:active {
  outline: none;
  box-shadow: none;
}

ul.res {
  padding: 0;
  margin: 0;
  list-style: none;
}

@font-face {
  font-family: "frt";
  src: url("../fonts/FrutigerLTArabic-55Roman.ttf") format("ttf"), url("../fonts/FrutigerLTArabic-55Roman.woff") format("woff"), url("../fonts/FrutigerLTArabic-55Roman.woff2") format("woff2"), url("../fonts/FrutigerLTArabic-55Roman.eot") format("eot");
}

@font-face {
  font-family: "frtBold";
  src: url("../fonts/FrutigerLTArabic-65Bold.ttf") format("ttf"), url("../fonts/FrutigerLTArabic-65Bold.woff") format("woff"), url("../fonts/FrutigerLTArabic-65Bold.woff2") format("woff2"), url("../fonts/FrutigerLTArabic-65Bold.eot") format("eot");
}

@font-face {
  font-family: "frtLight";
  src: url("../fonts/FrutigerLTArabic-45Light.ttf") format("ttf"), url("../fonts/FrutigerLTArabic-45Light.woff") format("woff"), url("../fonts/FrutigerLTArabic-45Light.woff2") format("woff2"), url("../fonts/FrutigerLTArabic-45Light.eot") format("eot");
}

@font-face {
  font-family: "frtBlk";
  src: url("../fonts/FrutigerLTArabic-75Black.ttf") format("ttf"), url("../fonts/FrutigerLTArabic-75Black.woff") format("woff"), url("../fonts/FrutigerLTArabic-75Black.woff2") format("woff2"), url("../fonts/FrutigerLTArabic-75Black.eot") format("eot");
}

@keyframes flash {
  from {
    opacity: .8;
  }
  to {
    opacity: 1;
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #FA5D1E;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.preloader .spinner {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 4px solid #FA5D1E;
  border-left: 4px solid #3d3d3d;
  animation: spin 1s linear infinite;
}

.menuTriger {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgba(250, 93, 30, 0.8);
  position: fixed;
  top: 25px;
  right: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 99;
  display: none;
}

html[dir="ltr"] .menuTriger {
  left: 30px;
  right: unset;
}

@media (max-width: 991px) {
  .menuTriger {
    display: flex;
  }
}

.menuTriger i {
  color: #ffffff;
  font-size: 20px;
}

.navover {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(61, 61, 61, 0.95);
  z-index: 9;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
}

.navover.open {
  opacity: 1;
  visibility: visible;
}

.sideNav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #3d3d3d;
  z-index: 99;
  overflow-y: auto;
  transition: 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transform-origin: right;
  transform: scaleX(0);
}

html[dir="ltr"] .sideNav {
  transform-origin: left;
}

.sideNav.open {
  transform: scaleX(1);
}

.sideNav.open ul {
  opacity: 1;
}

.sideNav.open ul li {
  transform: translateX(0);
  opacity: 1;
}

.sideNav.origin {
  transform-origin: left;
}

html[dir="ltr"] .sideNav.origin {
  transform-origin: right;
}

.sideNav .options {
  padding: 0 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  position: absolute;
  top: 50px;
}

.sideNav .options .lang a {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #fff;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: #FA5D1E;
}

.sideNav .options .search_pop {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: #FA5D1E;
  color: #fff;
  font-size: 14px;
  margin-left: auto;
  margin-right: 20px;
}

html[dir="ltr"] .sideNav .options .search_pop {
  margin-left: 20px;
  margin-right: auto;
}

.sideNav .options .close1 {
  display: flex;
  font-size: 18px;
  color: #FA5D1E;
  cursor: pointer;
}

.sideNav > ul {
  display: flex;
  align-items: center;
  flex-direction: column;
  opacity: 0;
  transition: 0.5s;
  transition-delay: 0.5s;
}

.sideNav > ul > li {
  position: relative;
  padding: 10px 0;
  transition: 0.5s;
  transform: translateX(10px);
  opacity: 0;
}

.sideNav > ul > li:nth-child(1) {
  transition-delay: 0.3s;
}

.sideNav > ul > li:nth-child(2) {
  transition-delay: 0.5s;
}

.sideNav > ul > li:nth-child(3) {
  transition-delay: 0.7s;
}

.sideNav > ul > li:nth-child(4) {
  transition-delay: 0.9s;
}

.sideNav > ul > li:nth-child(5) {
  transition-delay: 1.1s;
}

.sideNav > ul > li:nth-child(6) {
  transition-delay: 1.3s;
}

.sideNav > ul > li a {
  width: 100%;
  display: block;
  font-size: 20px;
  color: #FFF;
  transition: 0.5s;
}

.sideNav > ul > li i {
  position: absolute;
  top: 13px;
  left: 13px;
  transform: rotate(90deg);
  font-size: 9px;
  color: currentColor;
  transition: 0.5s;
}

html[dir="ltr"] .sideNav > ul > li i {
  left: unset;
  right: 13px;
}

.sideNav > ul > li i.open {
  transform: rotate(0deg);
}

.sideNav > ul > li .drop {
  width: 100%;
  padding: 5px 15px;
  display: none;
  overflow: visible;
  height: auto;
}

.sideNav > ul > li .drop li {
  padding: 4px 0;
}

.sideNav > ul > li .drop li a {
  font-size: 15px;
  color: #898989;
  transition: 0.5s;
}

.sideNav > ul > li .drop li a:hover {
  color: #595959;
}

